import { ApplicationMemoryRecord } from "./application_memory_record.js"

export class PieceYomiage extends ApplicationMemoryRecord {
  static get define() {
    return [
      { key: "K", prefix_name: "gyoku",  name: "gyoku",      promoted_name: null,           },
      { key: "R", prefix_name: "", name: "", promoted_name: "",     },
      { key: "B", prefix_name: "",   name: "",   promoted_name: "",     },
      { key: "G", prefix_name: "",   name: "",   promoted_name: null,           },
      { key: "S", prefix_name: "",   name: "",   promoted_name: "", },
      { key: "N", prefix_name: "",   name: "",   promoted_name: "", },
      { key: "L", prefix_name: "", name: "",   promoted_name: "", },
      { key: "P", prefix_name: "",     name: "",       promoted_name: "",   },
    ]
  }

  yomiage(promoted) {
    if (promoted) {
      return this.promoted_name
    } else {
      return this.name
    }
  }
}
