import { ApplicationMemoryRecord } from "./application_memory_record.js"
import { Location } from "./location"

export class EditToolInfo extends ApplicationMemoryRecord {
  static get define() {
    return [

      { name: "  ", func: e => { e.xcontainer.hold_pieces_to_piece_box(Location.fetch("white")) }, },
      { name: "  ", func: e => { e.xcontainer.piece_box_to_hold_pieces(Location.fetch("white")) }, },
      { separator: true },

      { name: ": ", func: e => { e.xcontainer.piece_box_reset_by_preset("") }, },
      { name: ": ", func: e => { e.xcontainer.piece_box_clear() }, },
      { name: ": ", func: e => { e.xcontainer.piece_box_piece_counts_adjust() }, },
      // { name: "",       func: e => { e.api_viewpoint_flip() }, },
      { separator: true },

      { name: ": ",     func: e => { e.xcontainer.king_formation_auto_set()   }, },
      { name: ": ",     func: e => { e.xcontainer.king_formation_auto_unset() }, },
      // { name: "()", func: e => { e.xcontainer.king_formation_set("bottom_left") }, },
      // { name: "()", func: e => { e.xcontainer.king_formation_unset("bottom_left") }, },
      // { name: "()", func: e => { e.xcontainer.king_formation_set("bottom_right") }, },
      // { name: "()", func: e => { e.xcontainer.king_formation_unset("bottom_right") }, },
      { separator: true },

      // { name: "",   func: e => { e.xcontainer.shuffle_apply(4) }, },
      { name: "",     func: e => { e.shuffle_dialog_open_handle() }, },
      { separator: true },

      { name: " ", func: e => { e.xcontainer.slide_xy(-1, 0) }, },
      { name: " ", func: e => { e.xcontainer.slide_xy(1, 0)  }, },
      { name: " ", func: e => { e.xcontainer.slide_xy(0, -1) }, },
      { name: " ", func: e => { e.xcontainer.slide_xy(0, 1)  }, },
    ]
  }
}
