// |-------------------+--------------------------+------------------------------------+------------------------------------+------|

.ShogiPlayer
  // 
  +defvar(sp_board_color, hsla(0, 0%, 0%, 0.2))      // 
  +defvar(sp_board_even_cell_color, transparent) // 
  +defvar(sp_board_odd_cell_color,  transparent) // 
  +defvar(sp_board_image, none)                    // 

  +defvar(sp_board_padding, 0.015)                 // 
  +defvar(sp_board_radius, 5)                      // 

  +defvar(sp_grid_outer_stroke, 0)                 // ()
  +defvar(sp_grid_outer_color, hsla(0, 0%, 0%, 0.5)) // 
  +defvar(sp_grid_inner_color, hsla(0, 0%, 0%, 0.5))       // 
  +defvar(sp_grid_inner_stroke, 1)                       // 
  +defvar(sp_board_edge_stroke, 0)    // ()
  +defvar(sp_star_size, 0.1)                  // 
  +defvar(sp_star_z_index, 0)                 //  z-index ( -1 )

  .MainBoard
    width: 100%
    height: 100%
    +is_overlay_origin

  .BoardTexture
    background-color: var(--sp_board_color)  // 
    +is_background_cover_by_image
    background-image: var(--sp_board_image)  // none 
    // background-image: url("../assets/inspect/256x256.png")

    border-radius: calc(var(--sp_board_radius) * 1px)
    border: calc(var(--sp_board_edge_stroke) * 1px) solid var(--sp_board_edge_color, var(--sp_grid_outer_color)) // 

  .BoardMatrixWithPadding
    padding: calc(var(--sp_board_padding) * 100%)

  //- flex  table, tr, td 
  .BoardMatrix
    width: 100%
    height: 100%
    border: calc(var(--sp_grid_outer_stroke) * 1px) solid var(--sp_grid_outer_color)
    border-collapse: collapse // tdborder

    // ()
    @extend %is_unselectable

    table-layout: fixed    // 

  .BoardCell
    // 
    border: calc(var(--sp_grid_inner_stroke) * 1px) solid var(--sp_grid_inner_color) // border-collapse: collapse 

    // 
    // Google Chrome 90.0.4430.216 
    // Google Chrome 91.0.4472.77  1.2
    // Firefox 
    //  Firefox 
    //  flex  height: 100% 
    height: calc(100.0% / var(--sp_board_dimension_h))

    &.even
      background-color: var(--sp_board_even_cell_color)
    &.odd
      background-color: var(--sp_board_odd_cell_color)

  // border  BoardTexture 
  // td
  //   +is_overlay_origin
  //   .CellBorder
  //     +is_overlay_block
  //     // border: calc(var(--sp_grid_inner_stroke) * 1px) solid var(--sp_grid_inner_color)

  // 
  .BoardRow:nth-child(3n+4)
    .BoardCell:nth-child(3n+4)
      position: relative
      &:after
        position: absolute
        content: ""
        // "%" 
        // sp_cell_h 
        // 
        top:    calc(var(--sp_cell_h) * var(--sp_star_size) * -0.5 - 0.5px)
        left:   calc(var(--sp_cell_h) * var(--sp_star_size) * -0.5 - 0.5px)
        width:  calc(var(--sp_cell_h) * var(--sp_star_size))
        height: calc(var(--sp_cell_h) * var(--sp_star_size))
        border-radius: 50%
        background-color: var(--sp_star_color, var(--sp_grid_outer_color))
