import { ApplicationMemoryRecord } from "./application_memory_record.js"
import { PresetInfo } from "./preset_info.js"

export class PresetDropdownInfo extends ApplicationMemoryRecord {
  static get define() {
    return [
      { key: "",                      },
      { separator: true,                  },
      { key: "",                    },
      { key: " - ",             },
      { key: " - ",             },
      { separator: true,                  },
      { key: " -  vs ", },
      { key: " - ",           },
      { separator: true,                  },
      { key: "",                  },
      { separator: true,                  },
      { key: "",                    },
      { key: "",                  },
      { key: "",                    },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                  },
      { key: "",                },
      { key: "",                },
      { separator: true,                  },
      { key: "",                  },
    ]
  }

  get preset_info() {
    if (this.separator) {
      return
    }
    return PresetInfo.fetch(this.key)
  }
}
