<template lang="pug">
.MembershipLocationPlayerInfo(v-if="show_p" :class="component_class" @click="click_handle")
  .MembershipLocationPlayerInfoName(v-html="player_name" v-if="player_name")
  .MembershipLocationPlayerInfoTime.is-family-monospace(v-html="player_time" v-if="player_time")
</template>

<script>
import { support } from "../support.js"

export default {
  name: "MembershipLocationPlayerInfo",
  mixins: [support],
  inject: ["ms"],
  methods: {
    player_attr_of(key) {
      if (this.one_side_info) {
        return this.one_side_info[key]
      }
    },
    click_handle() {
      this.TheSp.event_call("ev_action_player_info_click", this.ms.location, this.one_side_info)
    },
  },
  computed: {
    show_p() {
      return this.player_name || this.player_time
    },
    component_class() {
      return [
        this.player_class,
      ]
    },
    one_side_info() {
      if (this.TheSp.sp_player_info) {
        return this.TheSp.sp_player_info[this.ms.location.key]
      }
    },
    player_name()  { return this.player_attr_of("name")  },
    player_time()  { return this.player_attr_of("time")  },
    player_class() { return this.player_attr_of("class") },
  },
}
</script>

<style lang="sass">
@import "../support.sass"
.ShogiPlayer
  +defvar(sp_player_name_size, 0.25)       // 
  +defvar(sp_player_time_size, 0.25)       // 

  .MembershipLocationPlayerInfo
    font-size: calc(var(--sp_cell_h) * var(--sp_player_name_size))
    word-break: break-all
    flex-shrink: 0 // (100%1)

    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    gap: 0.2em                   // 

  // 
  .Membership
    &.is_turn_inactive
      .MembershipLocationPlayerInfo
        font-weight: normal
    &.is_turn_active
      .MembershipLocationPlayerInfo
        font-weight: bold

  +IF_HORIZONTAL                            // 
    &.is_name_direction_vertical           // 
      .MembershipLocationPlayerInfoName
        writing-mode: vertical-rl
  +IF_VERTICAL                              // ()
    &.is_name_direction_vertical           // 
      .MembershipLocationPlayerInfoName
        writing-mode: horizontal-tb         // 

  .MembershipLocationPlayerInfoTime
    white-space: nowrap         // 
    font-size: calc(var(--sp_cell_h) * var(--sp_player_time_size))

  &.is_balloon_on
    .MembershipLocationPlayerInfo
      @extend %is_piece_count_color_set
      padding: 0.5em
      border-radius: 3px

  &.is_layer_on
    .MembershipLocationPlayerInfo
      +is_layer_border

  .MembershipLocationPlayerInfo
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center

  .is_position_north
    .MembershipLocationPlayerInfo
      +is_flip

  .is_position_south
    .MembershipLocationPlayerInfo
      __css_keep__: 0

  // 
  +IF_HORIZONTAL
    .MembershipLocationPlayerInfo
      max-width: var(--sp_cell_w)
      line-height: 110%         // 100%

  // 
  +IF_VERTICAL
    .MembershipLocationPlayerInfo
      max-width: unset
      line-height: 140%
</style>
